<template>
  <div class="data-table-wrapper">
    <div class="data-table-toolbar">
      <div class="datatable-search-wrapper">
        <base-input
          type="text"
          name="search"
          id="clientSearch"
          class="datatable-search"
          v-model="searchQuery"
          placeholder="Zoeken..."
        />
        <base-icon iconRef="search" />
      </div>
      <div class="onlyOwnClientsToggle">
        <input
          name="onlyOwnedClients"
          id="onlyOwnedClients"
          class="checkbox"
          type="checkbox"
          v-model="onlyOwnedClients"
        >
        <label
          class="label"
          for="onlyOwnedClients"
        >Alleen klanten van mij</label>
      </div>
    </div>
    <div class="datatable-wrapper">
      <table
        class="page-datatable"
        v-if="dataList.length != 0"
      >
        <thead>
          <tr>
            <th
              v-for="column in columns"
              :key="column.property"
              class="sortable"
              :class="{sorting : propertyToSortOn == column.property}"
              @click="sort(column.property)"
            >
              <span>{{column.label}}</span>
              <base-icon
                iconRef="chevron-down"
                v-show="sortDirection == 'down'"
                position="right"
              />
              <base-icon
                iconRef="chevron-up"
                v-show="sortDirection == 'up'"
                position="right"
              />
            </th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="dataEntry in sortedDataList"
            :key="dataEntry.id"
            @click="$emit('click', dataEntry.id)"
          >
            <td>
              <span>{{dataEntry.data().clientName}}</span>
            </td>
            <td>
              <span>{{`${dataEntry.data().contactFirstName} ${dataEntry.data().contactLastName}`}}</span>
            </td>
            <td>
              <span>{{dataEntry.data().salesOwner}}</span>
            </td>
            <td>
              <span>{{dataEntry.data().createdAt.seconds | $prettifyDateToNumbers}}</span>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <p
      class="page-datatable-disclaimer"
      v-show="sortedDataList.length == 0"
    >Er zijn geen klanten gevonden.</p>
  </div>
</template>

<script>
export default {
  name: "clients-data-list",
  props: {
    dataList: {
      type: Array
    }
  },
  data: function() {
    return {
      propertyToSortOn: "clientName",
      sortDirection: "down",
      searchQuery: "",
      onlyOwnedClients: false,
      columns: [
        {
          label: "Klantnaam",
          property: "clientName"
        },
        {
          label: "Contactpersoon",
          property: "contactFirstName"
        },
        {
          label: "Sales eigenaar",
          property: "salesOwner"
        },
        {
          label: "Aangemaakt",
          property: "createdAt"
        }
      ]
    };
  },
  computed: {
    sortedDataList: function() {
      let dataList = this.dataList;
      if (this.searchQuery != "") {
        dataList = dataList.filter(item => {
          const clientName = item.data().clientName;
          const contactName =
            item.data().contactFirstName + " " + item.data().contactLastName;
          return (
            clientName.toLowerCase().includes(this.searchQuery.toLowerCase()) ||
            contactName.toLowerCase().includes(this.searchQuery.toLowerCase())
          );
        });
      }
      if (this.onlyOwnedClients) {
        dataList = dataList.filter(item => {
          return item.data().salesOwner == this.currentUserFullName;
        });
      }
      return this.$sortClientsAlphabetically(
        dataList,
        this.propertyToSortOn,
        this.sortDirection
      );
    },
    currentUser: function() {
      return this.$store.getters.getUsersList.find(user => {
        return user.data().uid == this.$store.getters.getUser.uid;
      });
    },
    currentUserFullName: function() {
      return (
        this.currentUser.data().firstName +
        " " +
        this.currentUser.data().lastName
      );
    }
  },
  methods: {
    sort: function(property) {
      if (this.propertyToSortOn != property) {
        this.sortDirection = "down";
        this.propertyToSortOn = property;
      } else if (
        this.propertyToSortOn == property &&
        this.sortDirection == "down"
      ) {
        this.sortDirection = "up";
      } else if (
        this.propertyToSortOn == property &&
        this.sortDirection == "up"
      ) {
        this.sortDirection = "down";
      }
      this.propertyToSortOn = property;
    }
  }
};
</script>

<style>
</style>



